:root {
  --primary-color: #ff385c;
}
* {
  padding: 0;
  margin: 0;
}
a {
  color: #484848;
  text-decoration: none;
}
img {
  vertical-align: top ;
}
ul,
li {
  list-style: none;
}
body {
  font-size: 14px;
  font-family: 'Circular', 'PingFang-SC', "Hiragino Sans GB", '微软雅黑', "Microsoft YaHei", "Heiti SC";
  color: #484848;
  line-height: 1.15;
}
